import React from 'react';

interface IBalanceInfo {
    name: string,
    amount: number,
    unit: string,
}
const BalanceInfo = (props: IBalanceInfo) => {
    return (
        <>
            <p style={{ color: "#FFFFFFA6" }}>{props.name}: <span style={{ color: "#FFFFFF" }}>{props.amount.toLocaleString('en-US')}</span> {props.unit}</p>
            {/* <p style={{ color: "#FFFFFFA6" }}>{props.name}: <span style={{ color: "#FFFFFF" }}>{props.amount.toFixed(2)}</span> {props.unit}</p> */}
        </>
    );
}

export default BalanceInfo;
