import React from 'react';
import { Modal, Button } from 'antd';
import './MessageModal.css';

interface IMessageModalProp {
    title?: any;
    visible: boolean;
    content: string,
    onClose?(): void;
    [name: string]: any;
}

const MessageModal = (props: IMessageModalProp) => {
    return (
        <div>
            <Modal
                title={props.title}
                centered
                closable={false}
                visible={props.visible}
                onOk={props.onClose}
                onCancel={props.onClose}
                bodyStyle={{ background: "#2b2b2b", color: "#FFFFFF", padding: "8px 15px", textAlign: "justify" }}
                footer={[
                    <Button key="close" onClick={props.onClose} style={{ background: "#2b643a", border: "none", color: "#FFFFFF"}}>
                        OK
                    </Button>,
                ]}>
                {props.content}
            </Modal>
        </div>

    );
};

export default MessageModal;