import axios from 'axios'


const handleAddressList = (addresses: string[]) => {
  let content = '';
  for (const index in addresses) {
    if (content != '') {
      content += ',';
    }
    content += `"${addresses[index]}"`;
  }
  return content;
}

export const getTokenPrice = async (addresses: string[]) => {
  addresses.push('0xa71edc38d189767582c38a3145b5873052c3e47a');

  const price: { [name: string]: number } = {};
  let content = handleAddressList(addresses);

  await axios.post(
    'https://graph.mdex.com/subgraphs/name/mdex/swap',
    {
      "operationName": "tokens",
      "variables": {},
      // "query": `fragment TokenFields on Token {\n  id\n  name\n  symbol\n  derivedETH\n  __typename\n}\nquery tokens {\n  tokens(orderBy: tradeVolumeUSD, orderDirection: desc, where: {id_in: [\n    \"0xa71edc38d189767582c38a3145b5873052c3e47a\", \n    \"${address}\", \n  ]}) {\n    ...TokenFields\n    __typename\n  }\n}`
      "query": `fragment TokenFields on Token {\n  id\n  name\n  symbol\n  derivedETH\n  __typename\n}\nquery tokens {\n  tokens(orderBy: tradeVolumeUSD, orderDirection: desc, where: {id_in: [${content}]}) {\n    ...TokenFields\n    __typename\n  }\n}`
    },
  ).then((response: any) => {
    // console.log(response)
    const tokens = response.data.data.tokens;

    let usdtPrice = 0;
    let tokenPrice = 0;
    for (const index in tokens) {
      if (tokens[index].symbol === 'USDT') {
        usdtPrice = tokens[index].derivedETH;
      } else {
        tokenPrice = tokens[index].derivedETH;
        price[tokens[index].id] = tokenPrice / usdtPrice;
      }
    }
    // console.log(`token price is: ${price}`);
  }, (error: any) => {
    console.log(`get token price error: ${error}`);
  })
  return price;
}

export const getLPPrice = async (addresses: string[]) => {
  const price: { [name: string]: number } = {};
  let content = handleAddressList(addresses);

  await axios.post(
    'https://graph.mdex.com/subgraphs/name/mdex/swap',
    {
      "operationName": "pairs",
      "variables": {
      },
      "query": `query pairs {\n  pairs(where: {id_in: [${content}]}) {\n    id\n    reserve0\n  reserve1\n  reserveUSD\n    totalSupply\n __typename\n  }\n}`
    },
  ).then((response: any) => {
    // console.log(response);
    const { pairs } = response.data.data;

    for (const index in pairs) {
      const pair = pairs[index];
      price[pair.id] = pair.reserveUSD / pair.totalSupply;
    }
  }, (error: any) => {
    console.log(`get lp price error: ${error}`);
  })
  return price;
}


export const getTokenAndLPPrice = async (tokenAddresses: string[], lpAddressed: string[]) => {
  tokenAddresses.push('0xa71edc38d189767582c38a3145b5873052c3e47a');

  const price: { [name: string]: number } = {};
  let tokenContent = handleAddressList(tokenAddresses);
  let lpContent = handleAddressList(lpAddressed);

  await axios.post(
    'https://graph.mdex.com/subgraphs/name/mdex/swap',
    {
      "operationName": "pairs",
      "variables": {
      },
      "query": `query pairs {\n  pairs(where: {id_in: [${lpContent}]}) {\n    id\n    reserve0\n  reserve1\n  reserveUSD\n    totalSupply\n __typename\n  }\ntokens(orderBy: tradeVolumeUSD, orderDirection: desc, where: {id_in: [${tokenContent}]}) {\n id\n  name\n  symbol\n  derivedETH\n  __typename\n  __typename\n}\n}`
    },
  ).then((response: any) => {
    console.log(response);
    if (response.data.data.hasOwnProperty('error')) {
      return null;
    }

    const { tokens, pairs } = response.data.data;

    let usdtPrice = 0;
    let tokenPrice = 0;
    for (const index in tokens) {
      if (tokens[index].symbol === 'USDT') {
        usdtPrice = tokens[index].derivedETH;
      } else {
        tokenPrice = tokens[index].derivedETH;
        price[tokens[index].id] = tokenPrice / usdtPrice;
      }
    }

    for (const index in pairs) {
      const pair = pairs[index];
      price[pair.id] = pair.reserveUSD / pair.totalSupply;
    }
  }, (error: any) => {
    console.log(`get token and lp price error: ${error}`);
  })
  return price;
}

export const getTokensPrice = async (
  contractMap: { [name: string]: any },
  configuration: { [name: string]: any }
) => {
  const { tokenAddress, usdtAddress, priceTokens, pools } = configuration;
  const usdtContract = contractMap[usdtAddress];

  const priceMap: { [token: string]: number } = {
    [usdtAddress]: 1,  //USDT
  };

  for (const index in priceTokens) {
    const { tokenAddress, usdtPairAddress } = priceTokens[index];
    const lpTokenBalance = await contractMap[tokenAddress].balanceOf(usdtPairAddress);
    const lpUsdtBalance = await usdtContract.balanceOf(usdtPairAddress);
    
    priceMap[tokenAddress] = (lpUsdtBalance) / (lpTokenBalance);
    // console.log(`lp token balance: ${lpTokenBalance}`);
    // console.log(`lp usdt balance: ${lpUsdtBalance}`);
    // console.log(`${tokenAddress} price: ${priceMap[tokenAddress]}\n`)
  }

  for (const index in pools) {
    const { lpAddress, type } = pools[index];
    if (type === 'token') {
      continue;
    }

    const lpTotalSupply = await contractMap[lpAddress].totalSupply();
    const lpTokenCount = await contractMap[tokenAddress].balanceOf(lpAddress);
    priceMap[lpAddress] = priceMap[tokenAddress] * (lpTokenCount) * 2 / (lpTotalSupply);
    // console.log(`lpTotalSupply: ${lpTotalSupply}`);
    // console.log(`lpTokenCount: ${lpTokenCount}`);
    // console.log(`${lpAddress} price: ${priceMap[lpAddress]}\n`)
  }

  // console.log(priceMap);
  return priceMap;
}