import React from 'react';
import { Skeleton } from 'antd';
import MediaQuery from 'react-responsive'
import styled from 'styled-components';
import { CaretRightOutlined } from '@ant-design/icons';
// import { Button } from 'antd';
import './StakeItem.css'

const Container = styled.div`
    cursor: pointer;
    padding: 16px 16px;
    overflow: hidden;
    transition: .2s background ease-out;
    align-items: center;
    border-bottom: 1px solid #181818;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    background: #2b2b2b;
    border-radius: 3px;
`;

const Logo = styled.img`
    width: 2.4rem;
    margin-right: 16px;
    display: inline-block;
`;

const Header = styled.div`
    flex-grow: 0;
    max-width: 20%;
    flex-basis: 20%;
    text-align: left;
    display: flex;
    align-items: center;
    justify-content: flex-start;
`;

const ColumnHeaderLeft = styled(Header)`
    max-width: 50%;
    flex-basis: 50%;
    text-align: left;
`;

const ColumnHeaderRight = styled(Header)`
    max-width: 50%;
    flex-basis: 50%;
    text-align: right;
    justify-content: flex-end;
`;

const Content = styled.div`
    flex-grow: 0;
    max-width: 16.666667%;
    flex-basis: 16.666667%;
    text-align: left;
`;

const LeftContent = styled.div`
    flex-grow: 0;
    max-width: 50%;
    flex-basis: 50%;
    text-align: right;
    padding-right: 16px;
    font-size: 14px;
`;

const RightContent = styled.div`
    flex-grow: 0;
    max-width: 50%;
    flex-basis: 50%;
    text-align: left;
    padding: 4px;
`;

const Button = styled.button`
    color: #121212;
    flex: 0 0 auto;
    padding: 12px;
    overflow: visible;
    font-size: 1.5rem;
    text-align: center;
    transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    border-radius: 50%;
    cursor: pointer;
    display: inline-flex;
    position: relative;
    align-items: center;
    vertical-align: middle;
    justify-content: center;
    text-decoration: none;
    background-color: transparent;
`;

interface IStakeItemProp {
    logo: string;
    title: string;
    tokens: number;
    yearRoi: number;
    value: number;
    onClick: any;
    loading: boolean;
    startTime: string;
    endTime: string;
}

interface IStakeItemState {
    status: string;
    timer: string;
}

class StakeItem extends React.Component<IStakeItemProp, IStakeItemState> {
    timer: any;
    constructor(props: IStakeItemProp) {
        super(props);

        this.state = {
            status: '',
            timer: '',
        }
    }

    componentDidMount() {
        this.timer = setInterval(() => {
            this.getPoolStatus();
        }, 1000);
    }
    componentWillUnmount() {
        clearInterval(this.timer);
    }

    getPoolStatus = async () => {
        const now = Date.now();
        const startTime = Date.parse(this.props.startTime);
        const endTime = Date.parse(this.props.endTime);

        if (startTime > now) {
            await this.setState({
                status: 'Not Started',
                timer: this.getCountdownContent(startTime - now),
            });
        } else if (now > endTime) {
            await this.setState({
                status: 'Finished',
                timer: '',
            })
        } else {
            await this.setState({
                status: 'Mining',
                timer: '',
            })
        }
    }

    formatTimestamp = (num: number) => {
        return num.toString().padStart(2, '0');
    }

    getCountdownContent = (timestamp: number) => {
        const MINUTE_TOMESTAMP: number = 60;
        const HOUR_TIMESTAMP: number = 60 * MINUTE_TOMESTAMP;
        const DAY_TIMESTAMP: number = 24 * HOUR_TIMESTAMP;

        timestamp = Math.floor(timestamp / 1000);

        const day = Math.floor(timestamp / DAY_TIMESTAMP);
        timestamp %= DAY_TIMESTAMP;

        const hour = Math.floor(timestamp / HOUR_TIMESTAMP);
        timestamp %= HOUR_TIMESTAMP;

        const minute = Math.floor(timestamp / MINUTE_TOMESTAMP);
        timestamp %= MINUTE_TOMESTAMP;

        const second = Math.floor(timestamp);

        const record = `${this.formatTimestamp(day)}:${this.formatTimestamp(hour)}:${this.formatTimestamp(minute)}:${this.formatTimestamp(second)}`;
        return record;
    }

    render() {
        return (
            <Container className="container" onClick={this.state.status == 'Not Started' ? null : this.props.onClick} key={this.props.title}>
                <MediaQuery minWidth={768}>
                    <Header style={{ textAlign: "left", maxWidth: "18%", flexBasis: "18%" }}>
                        <Logo src={this.props.logo}></Logo>
                        <p>{this.props.title}</p>
                    </Header>
                    <Content style={{ textAlign: "left", maxWidth: "24%", flexBasis: "24%" }}>
                        {this.props.loading ? <Skeleton.Avatar active={true} shape="square" style={{ width: 120, height: 35, borderRadius: 4 }}></Skeleton.Avatar> : <p>{this.props.tokens.toFixed(4)}</p>}
                    </Content>
                    <Content style={{ textAlign: "left", maxWidth: "24%", flexBasis: "24%" }}>
                        {this.props.loading ? <Skeleton.Avatar active={true} shape="square" style={{ width: 120, height: 35, borderRadius: 4 }}></Skeleton.Avatar> : <p>$ {this.props.value.toFixed(4)}</p>}
                    </Content>
                    <Content style={{ textAlign: "left", maxWidth: "20%", flexBasis: "20%" }}>
                        {this.props.loading ? <Skeleton.Avatar active={true} shape="square" style={{ width: 120, height: 35, borderRadius: 4 }}></Skeleton.Avatar> : <p>{this.props.yearRoi.toFixed(2)}%</p>}
                    </Content>
                    <Content style={{ textAlign: "left", maxWidth: "10%", flexBasis: "10%" }}>
                        {this.props.loading ? <Skeleton.Avatar active={true} shape="square" style={{ width: 120, height: 35, borderRadius: 4 }}></Skeleton.Avatar> : <div style={{ color: "#dda959" }}><p>{this.state.status}</p>{this.state.status == 'Not Started' && <p>{this.state.timer}</p>}</div>}
                    </Content>
                    <Content style={{ textAlign: "right", maxWidth: "4%", flexBasis: "4%"}}>
                        <Button className="button"><CaretRightOutlined /></Button>
                    </Content>
                </MediaQuery>
                <MediaQuery maxWidth={768}>
                    <ColumnHeaderLeft>
                        <Logo src={this.props.logo}></Logo>
                        <p>{this.props.title}</p>
                    </ColumnHeaderLeft>
                    <ColumnHeaderRight>
                        <Button className="button" onClick={this.state.status == 'Not Started' ? null : this.props.onClick}><CaretRightOutlined /></Button>
                    </ColumnHeaderRight>
                    <LeftContent><p style={{ fontWeight: 400 }}>Tokens Deposited</p></LeftContent>
                    <RightContent>
                        {this.props.loading ? <Skeleton.Avatar active={true} shape="square" style={{ width: 120, height: 30, borderRadius: 4 }}></Skeleton.Avatar> : <p>{this.props.tokens.toFixed(4)}</p>}
                    </RightContent>
                    <LeftContent><p style={{ fontWeight: 400 }}>Value</p></LeftContent>
                    <RightContent>
                        {this.props.loading ? <Skeleton.Avatar active={true} shape="square" style={{ width: 120, height: 30, borderRadius: 4 }}></Skeleton.Avatar> : <p>$ {this.props.value.toFixed(4)}</p>}
                    </RightContent>
                    <LeftContent><p style={{ fontWeight: 400 }}>APY</p></LeftContent>
                    <RightContent>
                        {this.props.loading ? <Skeleton.Avatar active={true} shape="square" style={{ width: 120, height: 30, borderRadius: 4 }}></Skeleton.Avatar> : <p>{this.props.yearRoi.toFixed(2)}%</p>}
                    </RightContent>
                    <LeftContent><p style={{ fontWeight: 400 }}>Status</p></LeftContent>
                    <RightContent>
                        {this.props.loading ? <Skeleton.Avatar active={true} shape="square" style={{ width: 120, height: 35, borderRadius: 4 }}></Skeleton.Avatar> : <div style={{ color: "#dda959" }}><p>{this.state.status}</p>{this.state.status == 'Not Started' && <p>{this.state.timer}</p>}</div>}
                    </RightContent>
                </MediaQuery>
            </Container>
        );
    }
};

export default StakeItem;
