import React from 'react';
import styled from 'styled-components';
import MediaQuery from 'react-responsive'
import { Spin } from 'antd';
import StakeItem from './StakeItem';
import { IPoolAssetData } from "../helpers/types";

const Content = styled.div``;

const ItemContainer = styled.div`
    border-bottom: 1px solid #181818;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    padding: 0;
`;

const ItemTitle = styled.div`
    flex-grow: 0;
    max-width: 33.333333%;
    flex-basis: 33.333333%;
`;

const ItemContent = styled.div`
    flex-grow: 0;
    max-width: 16.666667%;
    flex-basis: 16.666667%;
`;

interface IStakeListProps {
    loading: boolean;
    tvl: number;
    items: IPoolAssetData[];
    onClick(pool: IPoolAssetData): void;
}

interface IStakeListState { }


class StakeList extends React.Component<IStakeListProps, IStakeListState> {
    constructor(props: IStakeListProps) {
        super(props);
    }    

    render() {
        const stakeList = this.props.items.map((item: IPoolAssetData) => {
            return <StakeItem
                key={item.symbol}
                loading={this.props.loading}
                logo={item.logo}
                title={item.symbol}
                tokens={item.totalLockCount}
                yearRoi={item.apy}
                value={item.totalValueLocked}
                startTime={item.startTime}
                endTime={item.endTime}
                onClick={() => { this.props.onClick(item) }}>
            </StakeItem>
        });

        return (
            <>
                <MediaQuery minWidth={768}>
                    <ItemContainer style={{ marginTop: "2rem" }}>
                        <ItemTitle><p>All Setts - $ {this.props.tvl}</p></ItemTitle>
                    </ItemContainer>
                    <ItemContainer style={{padding: "4px 16px"}}>
                        <ItemContent style={{ textAlign: "left", maxWidth: "18%", flexBasis: "18%" }}></ItemContent>
                        <ItemContent style={{ textAlign: "left", maxWidth: "24%", flexBasis: "24%" }}><p className="stake-item" style={{ fontSize: "14px" }}>Locked</p></ItemContent>
                        <ItemContent style={{ textAlign: "left", maxWidth: "24%", flexBasis: "24%" }}><p className="stake-item" style={{ fontSize: "14px" }}>Value</p></ItemContent>
                        <ItemContent style={{ textAlign: "left", maxWidth: "20%", flexBasis: "20%" }}><p className="stake-item" style={{ fontSize: "14px" }}>APY</p></ItemContent>
                        <ItemContent style={{ textAlign: "left", maxWidth: "14%", flexBasis: "14%" }}><p className="stake-item" style={{ fontSize: "14px" }}>Status</p></ItemContent>
                    </ItemContainer>
                </MediaQuery>
                <MediaQuery maxWidth={768}>
                    <Content style={{ marginTop: "2rem" }}></Content>
                    <ItemTitle className="item-title"><p>All Setts - $ {this.props.tvl}</p></ItemTitle>
                </MediaQuery>

                {
                    this.props.loading ?
                        <div style={{ textAlign: "center", margin: "8px auto" }}><Spin></Spin></div> :
                        <div>{stakeList}</div>
                }
            </>
        );
    }
}

export default StakeList;
