import React from 'react';
import styled from 'styled-components'
import { Skeleton } from 'antd';


const Card = styled.div`
    width: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #FFFFFF;
    background-color: #2B2B2B;
    border-radius: 8px;
`;

interface INumberCardProp {
    firstValue: string,
    secondValue: number,
    style: any,
    loading: boolean,

}
const NumberCard = (props: INumberCardProp) => {
    return (
        <Card style={props.style}>
            <h6 style={{ color: "#FFFFFF", fontWeight: 400 }}>{props.firstValue}</h6>
            {props.loading ? <Skeleton.Avatar active={true} shape="square" style={{ width: 150, height: 35, borderRadius: 4 }}></Skeleton.Avatar> : <h5 style={{ color: "#FFFFFF" }}>$ {props.secondValue ? props.secondValue.toLocaleString('en-US') : 0}</h5>}
        </Card>
    );
};

export default NumberCard;