import React from 'react';
import MediaQuery from 'react-responsive';
import styled from 'styled-components';
import {
    Layout,
    Button,
    Menu,
    Drawer,
    Divider,
} from 'antd';

import {
    MenuUnfoldOutlined,
    MenuFoldOutlined,
    UserOutlined,
    TwitterOutlined,
    GithubOutlined,
    MailOutlined,
    WalletOutlined,
    MedicineBoxOutlined,
} from '@ant-design/icons';
import {
    ellipseAddress,
} from "../helpers/utilities";
import BalanceInfo from './BalanceInfo';
import MessageModal from './MessageModal';

import LogoImg from '../logo.png';
import Mining from '../images/mining.svg';
import Telegram from '../images/telegram.svg';

const { Sider } = Layout;
const { SubMenu } = Menu;


const SideMenuHeader = styled.header`
  background: #e7a748;
  padding: 0;
  color: #FFFFFF;
  height: 56px;
  width: 100%;
  padding: 0 1rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
`;

const Header = styled(SideMenuHeader)`
  top: 0;
  left: auto;
  right: 0;
  position: fixed;
  z-index: 1000;
  justify-content: space-between;
`;

const Logo = styled.img`
  height: 40px;
  width: 40px;
`;

const Content = styled.div``;

const Container = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
`;

const ConnectedStatus = styled.div`
  display: inline-block;
  width: 10px;
  height: 10px;
  background: #7FD1B9;
  margin-left: 5px;
  border-radius: 5px;
`;

const DisconnectedStatus = styled(ConnectedStatus)`
  background: #F4442E;
`;


interface ICusMenuProps {
    walletConnected: boolean;
    walletAddress: string;
    clkBalance: number;
    clkEarnings: number;
    handleConnect(): void;
    harvest(): void;
}

interface ICusMenuState {
    collapsed: boolean,
    drawerVisible: boolean,
    messageVisible: boolean,
    messageContent: any
    messageTitle: any,
    pending: boolean,
}

class CusMenu extends React.Component<ICusMenuProps, ICusMenuState> {

    constructor(props: ICusMenuProps) {
        super(props);

        this.state = {
            collapsed: false,
            drawerVisible: false,
            messageVisible: false,
            messageContent: '',
            messageTitle: '',
            pending: false
        };
    }

    showDrawer = () => {
        this.setState({
            drawerVisible: true,
        });
    }

    closeDrawer = () => {
        this.setState({
            drawerVisible: false,
        });
    }

    showMessage = (message: any, title?: any) => {
        this.setState({
            drawerVisible: false,
            messageVisible: true,
            messageContent: message,
            messageTitle: title,
        });
    }

    showIntro = () => {
        this.showMessage(
            'CloukLink is a decentralized community autonomous organization built on the Huobi ecosystem chain HECO. Its purpose is to introduce DOT to DEFI and accelerate the use of various products of Boca on DEFI. CLK is its governance token, with the right to submit proposals, vote, community benefit distribution, etc.',
            'Introduction'
        );
    };

    handleHarvest = async () => {
        await this.setState((state, props) => ({ pending: true }));
        try {
            await this.props.harvest();
        } finally {
            await this.setState((state, props) => ({ pending: false }));
        }
    }

    showPlan = () => {
        this.showMessage(
            <div>
                <p>1. Launching Dapp</p>
                <p>2. Starting DAO autonomous community, open community governance mining and other functions</p>
                <p>3. Mainstream centralized exchange</p>
                <p>4. Build community ecosystem</p>
            </div>,
            'Future Plan'
        );
    };

    render() {
        const menu = <>
            <Menu
                theme="dark"
                mode="inline"
                defaultOpenKeys={['sub']}
                style={{ padding: "0", fontWeight: 500, fontSize: '1rem' }}
            >
                <SideMenuHeader>
                    <Logo src={LogoImg}></Logo>
                </SideMenuHeader>
                <Content style={{ background: "#121212", color: "#FFFFFF", border: "none", width: "100%", lineHeight: 1.5 }}>
                    <Button style={{ background: "#121212", color: "#FFFFFF", border: "none", width: "100%", padding: "10px 15px", height: "auto", fontSize: "14px" }} onClick={this.props.handleConnect}>
                        <WalletOutlined />
                        {this.props.walletConnected ? ellipseAddress(this.props.walletAddress, 6) : 'CLICK TO CONNECT'}
                        {this.props.walletConnected ? <ConnectedStatus /> : <DisconnectedStatus />}
                    </Button>
                    <Content style={{ padding: "10px 15px" }}>
                        <BalanceInfo name="Balance" amount={this.props.clkBalance} unit="CLK"></BalanceInfo>
                        <BalanceInfo name="Earnings" amount={this.props.clkEarnings} unit="CLK"></BalanceInfo>
                        <Button
                            style={{ background: "#2b643a", color: "#FFFFFF", border: "none", width: "100%", padding: "6px 15px", height: "auto", fontSize: "1rem", marginTop: "6px" }}
                            onClick={this.handleHarvest}
                            loading={this.state.pending}>
                            Harvest
            </Button>
                    </Content>
                </Content>
                <Divider style={{ background: "#e0e0e0", margin: "8px 0" }}></Divider>

                <Menu.Item key="1" icon={<UserOutlined />} onClick={() => {
                    this.showMessage(<p>CloukLink community is under developing.</p>, 'CloukLink Community');
                }}>
                    CloukLink
                </Menu.Item>
                <Menu.Item key="2" icon={<img src={Mining} style={{ width: "14px", height: "14px" }}></img>} onClick={this.closeDrawer}>
                    Mining
                </Menu.Item>
                <Menu.Item key="9" icon={<MedicineBoxOutlined />} onClick={this.closeDrawer}>
                    <a href="https://cdn.jsdelivr.net/gh/CloudLinkk/CloudLinkk.github.io@main/AuditReport.pdf">Audit Report</a>
                </Menu.Item>

                <Divider style={{ background: "#e0e0e0", margin: "8px 0" }}></Divider>
                <SubMenu key="sub" title="About Us">
                    <Menu.Item key="3" onClick={this.showIntro}>Introduction</Menu.Item>
                    <Menu.Item key="4" onClick={this.showPlan}>Future Plans</Menu.Item>
                </SubMenu>
                <Menu.Item key="5" icon={<TwitterOutlined />}><a href="https://twitter.com/Prandom5">Twitter</a></Menu.Item>
                <Menu.Item key="6" icon={<img src={Telegram} style={{ width: "15px", height: "15px" }}></img>}><a href="https://t.me/clkndomdao">Telegram</a></Menu.Item>
                <Menu.Item key="7" icon={<GithubOutlined />}><a href="https://github.com/CloukLinkk">Github</a></Menu.Item>
                <Menu.Item key="8" icon={<MailOutlined />} onClick={() => {
                    this.showMessage(
                        <p>linda@cloudlink.hub</p>,
                        'Official Email');
                }}>Email</Menu.Item>
            </Menu>
        </>;

        return (
            <>
                <MediaQuery minWidth={768}>
                    <Sider trigger={null} collapsible collapsed={this.state.collapsed} style={{
                        background: "#121212",
                        overflow: 'auto',
                        height: '100vh',
                        position: 'fixed',
                        left: 0,
                    }}>
                        {menu}
                    </Sider>
                </MediaQuery>

                <MediaQuery maxWidth={768}>
                    <Header>
                        <Container><Logo src={LogoImg}></Logo></Container>

                        <Button
                            style={{ background: "transparent", border: "none" }}
                            onClick={this.showDrawer}>
                            {!this.state.drawerVisible ? <MenuFoldOutlined style={{ color: "black", fontSize: "24px" }} color="black" /> : <MenuUnfoldOutlined style={{ color: "black", fontSize: "24px" }} color="black" />}
                        </Button>
                    </Header>
                    <Drawer
                        // title={<Logo src={LogoImg} />}
                        placement="left"
                        closable={false}
                        onClose={this.closeDrawer}
                        visible={this.state.drawerVisible}
                        key="left"
                        style={{ padding: "0" }}
                    >
                        {menu}
                    </Drawer>
                </MediaQuery>

                <MessageModal
                    visible={this.state.messageVisible}
                    title={this.state.messageTitle}
                    content={this.state.messageContent}
                    onClose={() => { this.setState({ messageVisible: false, messageTitle: null, messageContent: null }) }}>
                </MessageModal>
            </>
        );
    }
}

export default CusMenu;
