import styled from 'styled-components';
import MediaQuery from 'react-responsive'
import NumberCard from './NumberCard';

const Content = styled.div``;

const Row = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
`;

const Column = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
`;

interface ITotalValueProps {
    tvl: number;
    portfolio: number;
    price: number;
    loading: boolean;
}

function TotalValue(props: ITotalValueProps) {
    return (
        <>
            <MediaQuery minWidth={768}>
                <Row>
                    <NumberCard firstValue="Total Value Locked" secondValue={props.tvl} style={{ width: "33%" }} loading={props.loading}></NumberCard>
                    <NumberCard firstValue="Your Locked Value" secondValue={props.portfolio} style={{ width: "33%" }} loading={props.loading}></NumberCard>
                    <NumberCard firstValue="CLK Price" secondValue={props.price} style={{ width: "33%" }} loading={props.loading}></NumberCard>
                </Row>
            </MediaQuery>

            <MediaQuery maxWidth={768}>
                <Column>
                    <NumberCard firstValue="Total Value Locked" secondValue={props.tvl} style={{ width: "100%", marginTop: "2rem" }} loading={props.loading}></NumberCard>
                    <NumberCard firstValue="Your Locked Value" secondValue={props.portfolio} style={{ width: "100%", marginTop: "0.5rem" }} loading={props.loading}></NumberCard>
                    <NumberCard firstValue="CLK Price" secondValue={props.price} style={{ width: "100%", marginTop: "0.5rem" }} loading={props.loading}></NumberCard>
                </Column>
            </MediaQuery>
        </>
    );
}

export default TotalValue;
